header {
    height: 100vh;
    padding-top: 0rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
    margin-top: -1rem ;
}

/* ======== CTA ========*/

.cta {
    margin-top: 1rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}
 /* ======== Header socials ======*/

 .header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 8rem;
 }

 .header__socials::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
 }

 /* =======ME====== */

 .me{
    background: linear-gradient( var(--color-primary),transparent) ;
    width: 29rem;
    height: 26rem;
    position:absolute;
    left: calc(50% - 14.5rem);
    margin-top: 2rem;
    border-radius: 6rem 6rem 0 0;
    overflow: hidden;
    padding: 0rem 2rem 4rem 2rem;
 }

 /* ======scroll down=====*/
 .scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 10rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
 }

/*======media query (medium device)=======*/
@media (max-width: 1024px) {
       header {
        height: 100vh;
       } 
       .header__socials {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 0.8rem;
        position: absolute;
        left: 0;
        bottom: 40rem;
     }
    
     .header__socials::after {
        content: "";
        width: 1px;
        height: 2rem;
        background: var(--color-primary);
     }

     .scroll__down{
        position: absolute;
        right: -2.3rem;
        bottom: 44rem;
        transform: rotate(90deg);
        font-weight: 300;
        font-size: 0.9rem;
     }
    
 
}

/*======media query (small device)=======*/
@media (max-width: 600px) {
    header {
        height: 120vh;
    }
    .me{
        width: 20rem;
        height: 23rem;
        position:absolute;
        left: calc(50% - 10rem);
        margin-top: 3rem;
        border-radius: 6rem 6rem 0 0;
        overflow: hidden;
        padding: 3rem 0.5rem 1rem 0.5rem;
    }


    .header__socials,
    .scroll__down {
        display:none;
    }
    
}