footer{
    background: var(--color-primary);
    padding: 5rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 1rem;
}
footer a{
    color: var(--color-bg);
}

.footer__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 2rem;
}
.footer__socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;

}

.footer__socials a:hover{
    background: transparent;
    color: var(--color-white);
    border-color: var(--color-white);
}

.footer__copy{
    margin-bottom: 2rem;
    color:var(--color-bg);
}




.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    display: inline-block;
    color: var(--color-bg);
    margin-bottom: 0.5rem;
}