.about-2 {
    flex-basis: 60%; 

}
.about__container {
    display: grid;
    grid-template-columns: 30% 50%;
    gap: 15%;
}
.about__container p{
    color: var(--color-light);
}

.about__me{
    width: 85%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,
    transparent,
    var(--color-primary),
    transparent);
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    transform: rotate(0);
}



.tab-titles{
    display: flex;
    margin: 20px 0 40px;
    
}
.tab-titles p{
    color: var(--color-white);
    font-size: 20px;
}
.tab-links{
    margin-right: 50px;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    position: relative;
    
}
.tab-links::after {
    content: '';
    width: 0;
    height: 3px;
    background: var(--color-primary);
    position: absolute;
    left: 0;
    bottom: -8px;
    transition: 0.5s;
}
.tab-links.active-link::after{
    width: 50%;
}
.tab-contents ul {
    list-style:none;
    margin: 30px 20px;
    font-size: 17px;
    font-style: oblique;
    line-height: 2;
    
}
.tab-contents ul p{
    font-size: 12px;
    font-style: italic;
}
.tab-contents{
    display: none;
}
.tab-contents.active-tab{
    display: block;
}

/*======media query (medium device)=======*/
@media (max-width: 1024px) {
.about__container{
    display:block;    
}
.about__me{
    width: 35%;
    margin: 2rem auto 4rem;
}
.about__container p{
    margin: 1rem 0 1.5rem;
}
.tab-titles{
    display:flex;
    margin: 10px 0 20px;
    gap: 40px;
}
.tab-contents ul {
    list-style:none;
    margin: 10px 10px;
    font-size: 17px;
    font-style: oblique;
    line-height:normal;
    
}
}

/*======media query (small device)=======*/
@media (max-width: 600px) {
    .about__container{
        display:block;    
    }
    .about__me{
        width: 45%;
        margin: 2rem auto 4rem;
    }
    .about__container p{
        margin: 1rem 0 1rem;
      
    }
  

    .tab-titles{
        display:flex;
        margin: 10px 0 20px;
        gap: 60px;
    }
    .tab-contents ul {
        list-style:none;
        margin: 10px 10px;
        font-size: 17px;
        font-style: oblique;
        line-height:normal;
        
    }
}