.about__cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
   grid-gap: 1rem;
    padding: 1rem 80px;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 1.2rem; 
    row-gap: 20px;
    margin-top: 4rem;
    width: 70rem;
    margin-left: 12rem;
}
.about__card{
    /* box-shadow: 0 5px 10px rgb(34, 33, 33);
    border-radius:2rem ;
    border: 1px solid transparent;
    background: var(--color-bg-variant);
    text-align: center;
    padding: 30px 20px; */

    padding: 1rem;
    color: var(--color-white);
    
}

.about__card:hover > .skill__tit:after, .about__card:hover > .skill__tit:before{
    width: 35px;
}

.about__card:hover .skill__img{
  transform: translateY(-10px);
}

.skill__tit{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0.5rem;
    margin-bottom: 1rem;
    position: relative;
    
}

.skill__tit:after{
   content: '';
   position: absolute;
   bottom: 0;
   right: 50%;
   width: 0;
   height: 4px;
   border-radius: 2px 0 0 2px;
   background-color: var( --color-primary);
   transition: .5s;
}
 
.skill__tit:before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 4px;
    border-radius: 0 2px 2px 0;
    background-color: var( --color-primary);
    transition: .5s;
}

.skill__icon{
    width: 50px;  
    z-index: 2; 
}

.skill__img{
    width: 90px;
    height: 90px;
    position: relative;
    border-radius: 45px;
    background-color: var(--color-white);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: .5s;
}

.skill__img:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 90px;
    background: rgba(100,100,100,0.5);
    border-radius: 45px 0 0 45px;
}
.butn{
    font-weight:bolder;
    font-size: 15px;
    margin-top: 12px;
    display: inline-block;
    color: var(--color-white);
    background: transparent;
    padding: 8px 25px;
    transition: var(--transition);
    cursor: pointer;
}


.about__card:hover{
    box-shadow:0 10px 15px rgba(0,0,0,.3);
    transform: scale(1.03);
    background: transparent;
}

